@import "../variables";

@include mobile {
  .messenger__sidebar-inner.opened {
    & + .wrapper {
      .messenger-header .lastseen {
        display: none;
      }
    }
  }
}

.messenger-header {
  width: 100%;
  height: 48px;
  position: relative;

  background-color: $color-main;
  border-bottom: $style-border;

  &:hover {
    cursor: pointer;
  }

  .avatar__wrapper {
    @include center-absolute;
    width: 35px;
    height: 35px;
    left: 10px;
  }

  .header-block {
    display: flex;
    flex-direction: column;
    position: absolute;
    height: 100%;
    padding-left: 60px;
    padding-right: 30px;

    .lastseen {
      white-space: nowrap;
    }
    .text-loader {
      span {
        color: #000;
      }
    }
  }

  .title {
    margin-top: 5px;
    font-weight: 600;
    display: flex;
    align-items: center;
    overflow: hidden;
    word-break: break-word;
  }

  .window-button {
    right: 0;
    @include center-absolute;

    @include mobile {
      & {
        right: 0px;
      }
      & img {
        width: 40px;
      }
    }
  }
}