
          @import "src/styles/_variables.scss";
          @import "src/styles/_constants.scss";
          @import "src/styles/_mixins.scss";
        
.accordion {
  &-body {
    transition: height 0.2s ease-in-out;
    overflow: hidden;
    &__container {
      width: 100%;
      height: 100%;
    }
  }

  &-header {
    border-radius: 6px;
    border: solid 1px rgba(128, 128, 128, 0.33);
    overflow: hidden;
    
    &:hover {
      cursor: pointer;
    }

    &__wrapper {
      display: flex;
      column-gap: 10px;
      justify-content: space-between;
      padding: 0 5px;
    }
  }
  .toggle-button {
    margin-left: auto;
  }
}