.info__paragraph{padding:8px 4px;margin:0 auto;word-wrap:break-word;word-break:break-word;display:inline-block}.info__paragraph li{counter-increment:count 1}.info__paragraph ul,.info__paragraph li{padding-left:1.5em;list-style-type:none}.info__paragraph li::before{display:inline-block;margin-left:-1.5em;margin-right:.3em;text-align:right !important}.info__paragraph ol li::before{content:counter(count, decimal) ". "}.info__paragraph ul li::before{content:"•"}.info__paragraph:empty{display:none;padding-bottom:0}.info__paragraph:hover{cursor:pointer}.info__paragraph-title{font-weight:700;font-size:20px;padding-bottom:0}.info__paragraph-description{white-space:pre-line;text-indent:0}.info__paragraph-date{width:auto;white-space:nowrap;font-size:18px;margin:0 20px}.info__paragraph .ql-editor{overflow:hidden}.info__paragraph .ql-simple .ql-toolbar{display:none}.info__paragraph .ql-simple .ql-editor{padding:5px;background-color:#ebeff2}