.masonry__grid {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    .action-button.masonry-view {
        position: sticky;
        margin-left: 10px;
        height: 40px;
        display: flex;
        align-items: center;
        column-gap: 10px;
        svg {
            width: 30px;
            filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(244deg) brightness(106%) contrast(100%);
        }
    }
}

$masonry-spacing: 6px;

.masonry__column {
    display: flex;
    flex-direction: column;
    padding: 0 $masonry-spacing 0 $masonry-spacing;
    row-gap: calc($masonry-spacing * 2);
}

.masonry__item-enter {
    opacity: 0;
    transform: translateX(100px);
    //transform: translateY(200px);
}
.masonry__item-enter-active {
    opacity: 1;
    transform: translateX(0);
    //transform: translateY(0);
    transition: all 300ms;
}
.masonry__item-exit {
    opacity: 1;
    //transform: translateY(0);
}
.masonry__item-exit-active {
    opacity: 0;
    //transform: translateY(200px);
    transition: all 300ms;
}