
          @import "src/styles/_variables.scss";
          @import "src/styles/_constants.scss";
          @import "src/styles/_mixins.scss";
        
.toggle-button {
  transition: all 232ms linear;
  width: 24px;
  height: 24px;
  &:hover {
    cursor: pointer;
  }
  &.closed {
    transform: rotate(0);
  }
  &.opened {
    transform: rotate(180deg);
  }
}